<template>
  <div class="login-main-content">
    <input id="pwd" class="pwd" type="password" placeholder="Mot de passe" v-model="password">
    <div class="login-toggle-pwd-container">
        <input id="toggle-pwd" type="checkbox" class="toggle-pwd-input" @click="displayPwd">
        <label for="toggle-pwd" class="toggle-pwd-label">Afficher le mot de passe</label>
    </div>
    <button class="login" @click="submit">Login</button>
    </div>
</template>

<script>
import axios from 'axios';

export default {

    data() {
        return {
            password: '',
            dbpassword: []
        }
    },
    methods: {
        displayPwd: function() {
            var input = document.getElementById("pwd");
            if(input.type == "password") {
                input.type = "text";
            } else {
                input.type = "password";
            }
        },
        getContent: async function () {
            try {
                const response = await axios.get("http://localhost:5000/users");
                for (let user in response.data) {
                    this.dbpassword.push(response.data[user].password)
                }
            } catch(err) {
                console.log(err);
            }
        },
        submit: async function () {
            try {
            await axios.post('http://localhost:5000/login', {
                pwd: this.password,
            })
            .then((response) => {
                const token = response.data.token;
                sessionStorage.setItem('admin', token);
                this.$router.push({path: '/admin/cours'});
            });
            }
            catch (err) {
                console.log(err.response.data.msg);
            }


            }

    },
    mounted() {
        this.getContent();
    }

}
</script>

<style lang="scss">
.login-main-content {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--dark);
    flex-direction: column;

    .pwd {
        width: 50%;
        height: 5%;
        border: none;
        outline: none;
        color: white;
        padding-left: 15px;
        border-radius: 15px 15px;
        background-color: var(--selected);
        font-size: var(--text-size-button-content);
    }

    .login-toggle-pwd-container {
        height: 8%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        .toggle-pwd-label {
            color: white;
            font-size: var(--text-size-button-content);
        }

        input[type="checkbox"] {
            appearance: none;
            background-color: var(--selected);
            margin: 0;
            margin-right: 15px;
            font: inherit;
            color: currentColor;
            width: calc(15px + 2vmin);
            height: calc(15px + 2vmin);
            border-radius: 5px 5px;
            display: grid;
            place-content: center;
        }

        input[type="checkbox"]::before {
            content: "";
            width: 15px;
            height: 15px;
            clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
            transform: scale(0);
            transform-origin: bottom left;
            transition: 120ms transform ease-in-out;
            box-shadow: inset 1em 1em white;
        }

        input[type="checkbox"]:checked::before {
            transform: scale(1);
        }
    }

    .login {
        margin-top: 2%;
        height: 5%;
        width: 15%;
        background-color: var(--selected);
        font-size: var(--text-size-button-content);
        color: white;
        border: none;
        border-radius: 15px 15px;
        cursor: pointer;
        transition: 0.5s;

        &:hover {
            transform: scale(1.2);
        }

    }
}
</style>